import React, { Component } from 'react';


const About = props => {
  return (
    <div id="bio" className="row">
      <div className="col d-flex flex-column justify-content-start align-items-center text-center p-5 pb-1" style={{ minHeight: '50vh', backgroundColor: '#26262D' }}>
        <div style={{ width: '80%' }}>
          <h1 className="text-white text-align-left pt-4 mt-4" style={{ textAlign: 'left', letterSpacing: '7px' }}>BIO</h1>
          <p className="about-p" style={{ fontSize: '2rem', lineHeight: '3.2rem' }}>
            I'm Michael, a full-stack software developer from Las Vegas, Nevada. I first started doing software
            development in 2015, and since then my passion for the industry has only grown. Since the
            beginning my career, I've had the opportunity of working to develop an E-Commerce store for a
            Las Vegas Company (Vapetasia), being a teaching assistant to help instruct students at UCLA to
            learn a Full-Stack coding curriculum as well as industry standards, and working at an SEO company
            to develop in-house software solutions to help manage the company and work with clients.
        </p>

        </div>
      </div>
    </div>
  );
}


export default About;


